<template>
    <div>
        <el-button icon="el-icon-edit" type="success" @click="outerVisible = true" round=""> Input</el-button>
        <el-dialog title="Insert Ad Data" :visible.sync="outerVisible" center>
            <!--- MODAL BODY -->
            <el-form ref="form" :model="form" label-width="120px">
                <el-form-item label="AdId">
                    <el-input
                            v-model="form.adId"
                            placeholder="12345"
                            suffix-icon="el-icon-edit"
                            size="medium">
                    </el-input>
                </el-form-item>
                <el-form-item label="Ad Type">
                    <el-select v-model="form.adType" size="large" placeholder="please select ad type">
                        <el-option
                                v-for="types in adTypes"
                                :key="types.value"
                                :label="types.label"
                                :value="types.value">
                            <span style="float: left">{{ types.label }}</span>
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="Platform">
                    <el-select v-model="form.platform" size="large" placeholder="please select platform">
                        <el-option
                                v-for="environment in environments"
                                :key="environment.value"
                                :label="environment.label"
                                :value="environment.value">
                            <span style="float: left">{{ environment.label }}</span>
                            <span style="float: right; color: #8492a6; font-size: 13px">{{ environment.value }}</span>
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <!--- END OF MODAL BODY -->
            <div slot="footer" class="dialog-footer">
                <el-button @click="outerVisible = false">Cancel</el-button>
                <el-button @click="submitData()">Submit</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import Types from '../store/types'
    import {ENVIRONMENTS, AD_DROPDOWN_TYPES, SID} from '../utils/constants'

    export default {
        data() {
            return {
                activeIndex: '2',
                outerVisible: false,
                environments: [],
                adTypes: [],
                form: {
                    adId: this.$store.getters.adId || null,
                    platform: this.$store.getters.platform || null,
                    adType: this.$store.getters.adType || null,
                }
            };
        },
        created() {
            this.environments = ENVIRONMENTS;
            this.adTypes = AD_DROPDOWN_TYPES;
        },
        methods: {
            async submitData() {
                await this.$store.dispatch(Types.actions.ACTION_UPDATE, {
                    adId: this.form.adId,
                    adType: this.form.adType,
                    platform: this.form.platform,
                    sid: SID,
                    forceHttpScheme: null,
                });
                this.outerVisible = false;
                window.location.reload();
            }
        }
    }
</script>

<style>
    .el-input__inner {
        width: 480px !important;
    }
</style>
