<template>
    <div>
        <div v-if="!isRenderAnyWhere && hasValidAdId">

            <el-row :gutter="20" v-if="isSuperHeader">
                <div class="grid-content bg-purple-light">
                    <div class="superHeader" :id="getElementId"></div>
                </div>
            </el-row>
            <br>


            <el-row :gutter="20">
                <el-col :span="12">
                    <div class="grid-content bg-purple"></div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <div v-if="isLeaderBoard" :id="getElementId"></div>
                    </div>
                </el-col>
            </el-row>
            <br>


            <el-row :gutter="20">
                <el-col :span="18">
                    <div class="grid-content bg-purple">
                        <div v-if="isBillBoard" :id="getElementId"></div>
                        <dummy-data v-else></dummy-data>
                    </div>
                    <br>
                    <div class="grid-content bg-purple">
                        <dummy-data></dummy-data>
                    </div>
                    <br>
                    <div class="grid-content bg-purple">
                        <dummy-data></dummy-data>
                    </div>
                    <br>
                    <div class="grid-content bg-purple">
                        <dummy-data></dummy-data>
                    </div>
                    <br>
                    <div class="grid-content bg-purple">
                        <dummy-data></dummy-data>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <div v-if="isHalfPage" class="halfpage" :id="getElementId"></div>
                        <dummy-data v-else></dummy-data>
                    </div>
                    <br>
                    <div class="grid-content bg-purple">
                        <dummy-data></dummy-data>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div v-if="isRenderAnyWhere">
            <div class="halfpage" :id="getElementId"></div>
        </div>
    </div>
</template>

<script>
    import DummyData from '../DummyData'
    import {SID, DEFAULT_PLATFORM, AD_TYPES} from '../../utils/constants'
    import postscribe from 'postscribe'

    export default {
        name: "Advertise",
        components: {DummyData},
        data() {
            return {
                adId: this.$store.getters.adId || null,
                isHalfPage: false,
                isLeaderBoard: false,
                isBillBoard: false,
                isSuperHeader: false,
                isRenderAnyWhere: false,
                platform: this.$store.getters.platform || DEFAULT_PLATFORM,
                sid: this.$store.getters.sid || SID,
                forceHttpScheme: ''
            }
        },
        computed: {
            getElementId: function () {
                return `ad-${this.adId}`;
            },

            hasValidAdId: function () {
                return this.$store.getters.adId !== null;
            },

            getAdSource: function () {
                return `${this.platform}/map/engine/dynamic?sid=${this.sid}&forceAdId=${this.adId}&forceHttpScheme=${this.forceHttpScheme}`;
            }
        },

        async mounted() {
            this.setFalseDataStatus();
            await this.setDataStatus();
            postscribe(`#ad-${this.adId}`, `<script type="text/javascript" src="${this.getAdSource}"><\/script>`)
        },

        methods: {
            setFalseDataStatus() {
                this.isHalfPage = false;
                this.isLeaderBoard = false;
                this.isBillBoard = false;
                this.isSuperHeader = false;
                this.isRenderAnyWhere = false
            },

            async setDataStatus() {
                let type = await this.$store.getters.adType;
                if (type === AD_TYPES.LEADERBOARD)
                    this.isLeaderBoard = true;
                else if (type === AD_TYPES.BILLBOARD)
                    this.isBillBoard = true;
                else if (type === AD_TYPES.SUPERHEADER)
                    this.isSuperHeader = true;
                else if (type === AD_TYPES.HALFPAGE)
                    this.isHalfPage = true;
                else if (type === AD_TYPES.RENDER)
                    this.isRenderAnyWhere = true;
                else
                    this.isRenderAnyWhere = true;
            }
        }
    }
</script>

<style lang="scss">
    .el-row {
        margin-bottom: 20px;

        &,
        :last-child {
            margin-bottom: 0;
        }

    }

    .el-col {
        border-radius: 4px;
    }

    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }

    .grid-content {
        border-radius: 4px;
        min-height: 36px;
    }

    .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
    }

    .halfpage {
        min-width: 300px !important;
        min-height: 600px !important;
    }
    .superHeader {
        width: 100% !important;
    }
</style>
