import Cookies from 'js-cookie'
import Types from '../types'

const index = {
  state: {
    adId: Cookies.get('adId'),
    adType: Cookies.get('adType'),
    platform: Cookies.get('platform'),
    sid: Cookies.get('sid'),
    forceHttpScheme: Cookies.get('forceHttpScheme')
  },

  getters: {
    adId: state => state.adId,
    adType: state => state.adType,
    platform: state => state.platform,
    sid: state => state.sid,
    forceHttpScheme: state => state.forceHttpScheme,
  },

  mutations: {
    [Types.mutations.UPDATE_DATA] (state, payload) {
      Cookies.set('adId', payload.adId);
      Cookies.set('adType', payload.adType);
      Cookies.set('platform', payload.platform);
      Cookies.set('sid', payload.sid);
      Cookies.set('forceHttpScheme', payload.forceHttpScheme);
    },
  },

  actions: {
    [Types.actions.ACTION_UPDATE] ({commit, dispatch}, payload) {
      commit(Types.mutations.UPDATE_DATA, payload)
    }
  }
};

export default index;
